body {
  background-color: #f0f0f0;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
}

.watch {
  box-sizing: border-box;
  border: 5px dotted #4f94b9;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.watch__circule {
  z-index: 1;
  background-color: #7d84c9;
  border-radius: 50px;
  width: 20px;
  height: 20px;
  position: absolute;
}

.watch__second-hand {
  transform-origin: bottom;
  background-color: #e2f514;
  width: 5px;
  height: 50%;
  animation: 5s linear 1s infinite rotate-animation;
  position: absolute;
  top: 0;
}

.watch__minute-hand {
  transform-origin: bottom;
  background-color: #13c9da;
  width: 9px;
  height: 25%;
  animation: 60s linear 1s infinite rotate-animation;
  position: absolute;
  top: 20vmin;
}

@keyframes rotate-animation {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

